.header {
    background-color: var(--primary-color);
    padding: 10px 0;
    color: var(--white-color);
  }
  
  .container {
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo {
    font-size: 1.5em;
  }
  
  .nav ul {
    list-style: none;
    display: flex;
    gap: 20px;
  }
  
  .nav a {
    color: var(--white-color);
    text-decoration: none;
    font-weight: bold;
  }
  
  .nav a:hover {
    color: var(--secondary-color);
  }
  .logo-container {
    display: flex;
    align-items: center;
  }
  
  .logo {
    max-width: 120px;
    height: auto;
  }
    