.main {
  background-color: var(--light-gray-color);
  padding-top: 20px;
  color: var(--primary-color);
}

.content-blocks {
  display: flex;
  justify-content: space-between;
  padding: 40px 0;
  background-color: var(--light-gray-color);
}

.block {
  flex: 1;
  margin: 0 15px;
  text-align: center;
  background-color: var(--white-color);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.block img {
  max-width: 30%;
  height: auto;
  margin-bottom: 15px;
}

.block h2 {
  color: var(--primary-color);
  margin-bottom: 10px;
}

.block p {
  color: var(--secondary-color);
}
