.footer {
    background-color: var(--secondary-color);
    padding: 10px 0;
    color: var(--white-color);
    text-align: center;
    font-family: 'Montserrat', sans-serif;
  }
  
  .footer p {
    margin: 0;
  }
  .footer-container {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
  }
  
  .footer-column {
    flex: 1;
  }
  
  .footer-column h4 {
    color: var(--white-color);
    margin-bottom: 10px;
  }
  
  .footer-column ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-column ul li {
    margin-bottom: 5px;
  }
  
  .footer-column ul li a {
    color: var(--white-color);
    text-decoration: none;
  }
  
  .footer-column ul li a:hover {
    color: var(--light-gray-color);
  }
  
  .social-icon {
    width: 20px;
    vertical-align: middle;
    margin-right: 10px;
  }
  
  .footer-bottom {
    text-align: center;
    padding: 10px 0;
    background-color: var(--primary-color);
    font-size: .8em;
  }