.quote-section {
    background-color: #f5f5f6;
    padding: 40px 0;
    text-align: center;
  }
  
  .quote-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: var(--white-color);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  blockquote {
    font-size: 1.5em;
    font-style: italic;
    margin: 0;
  }
  
  footer {
    margin-top: 10px;
    font-size: 1.2em;
    color: var(--primary-color);
  }
  