/* General styles */
body {
  font-family: 'Montserrat', sans-serif;
}

h1, h2, h3 {
  font-family: 'Montserrat', serif;
  text-align: center;
}

.nav a {
  font-family: 'Poppins', sans-serif;
}

/* Header */
.header {
  background-color: var(--primary-color);
  padding: 20px 0;
  color: var(--white-color);
}

.container {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo {
  max-width: 150px;
  height: auto;
}

/* Navigation */
.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav a {
  color: var(--white-color);
  text-decoration: none;
  font-weight: bold;
}

.nav a:hover {
  color: var(--secondary-color);
}

/* Main Image Section */
.main-image-section {
  position: relative;
  width: 100%;
  height: 400px;
  overflow: hidden;
}

.main-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(20, 48, 95, 0.5); /* Dark overlay */
  color: var(--white-color);
  text-align: center;
}

/* Content Blocks */
.content-blocks {
  display: flex;
  justify-content: space-between;
  padding: 40px 0;
  background-color: var(--light-gray-color);
}

.block {
  flex: 1;
  margin: 0 15px;
  text-align: center;
  background-color: var(--white-color);
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.block img {
  max-width: 100%;
  height: auto;
  margin-bottom: 15px;
}

.block h2 {
  color: var(--primary-color);
  margin-bottom: 10px;
}

.block p {
  color: var(--secondary-color);
}

/* Footer */
.footer {
  background-color: var(--secondary-color);
  padding: 20px 0;
  color: var(--white-color);
}

.footer-container {
  display: flex;
  justify-content: space-between;
  padding: 20px 0;
}

.footer-column {
  flex: 1;
}

.footer-column h4 {
  color: var(--white-color);
  margin-bottom: 10px;
}

.footer-column ul {
  list-style: none;
  padding: 0;
}

.footer-column ul li {
  margin-bottom: 5px;
}

.footer-column ul li a {
  color: var(--white-color);
  text-decoration: none;
}

.footer-column ul li a:hover {
  color: var(--light-gray-color);
}

.social-icon {
  width: 20px;
  vertical-align: middle;
  margin-right: 10px;
}

.footer-bottom {
  text-align: center;
  padding: 10px 0;
  background-color: var(--primary-color);
}

/* Responsive Styles */
@media (max-width: 768px) {
  .container {
    flex-direction: column;
    align-items: flex-start;
  }

  .nav ul {
    flex-direction: column;
    gap: 10px;
  }

  .content-blocks {
    flex-direction: column;
  }

  .block {
    margin: 15px 0;
  }

  .footer-container {
    flex-direction: column;
    align-items: flex-start;
  }

  .footer-column {
    margin-bottom: 20px;
  }
}

@media (max-width: 480px) {
  .logo {
    max-width: 120px;
  }

  .main-image-section {
    height: 300px;
  }

  .overlay {
    font-size: 14px;
  }

  .footer-column ul li a {
    font-size: 14px;
  }
}