.team-section {
    padding: 40px 0;
    text-align: center;
    background-color: var(--white-color);
  }
  
  .team-section h2 {
    color: var(--primary-color);
    margin-bottom: 20px;
  }
  
  .team-container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .team-member {
    flex: 1;
    max-width: 220px;
    margin: 0 auto;
    text-align: center;
    background-color: var(--light-gray-color);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .team-image {
    width: 100%;
    height: auto;
    border-radius: 50%;
    margin-bottom: 10px;
  }
  
  .team-member h3 {
    color: var(--primary-color);
    margin: 10px 0;
  }
  
  .team-member p {
    color: var(--secondary-color);
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .team-container {
      flex-direction: column;
      align-items: center;
    }
  
    .team-member {
      max-width: 100%;
      margin-bottom: 20px;
    }
  }
  
  @media (max-width: 480px) {
    .team-member {
      padding: 15px;
    }
  
    .team-image {
      width: 80%;
    }
  }
  