.main-image-section {
    position: relative;
    width: 100%;
    height: 400px;
    overflow: hidden;
  }
  
  .main-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(20, 48, 95, 0.5); /* Dark overlay */
    color: var(--white-color);
    text-align: center;
    font-size: 2em;
  }

  .subtitle {
    color: white;
  }
  