.contact-us {
    padding: 40px 20px;
    background-color: var(--light-gray-color);
  }
  
  .contact-header {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .contact-header h2 {
    color: var(--primary-color);
    font-size: 2em;
  }
  
  .contact-content {
    display: flex;
    flex-wrap: wrap;
    gap: 200px;
    justify-content: center;
  }
  
  .map-container {
    flex: 1 1 45%; /* Map takes up about 45% of the width */
    max-width: 600px;
  }
  
  .contact-form {
    flex: 1 1 45%; /* Form takes up about 45% of the width */
    max-width: 600px;
    display: flex;
    flex-direction: column;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    margin-bottom: 5px;
    display: block;
    color: var(--secondary-color);
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid var(--secondary-color);
    border-radius: 4px;
  }
  
  textarea {
    resize: vertical; /* Allow resizing vertically but not horizontally */
  }
  
  button[type="submit"] {
    padding: 10px 20px;
    background-color: var(--primary-color);
    color: var(--white-color);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  button[type="submit"]:hover {
    background-color: var(--secondary-color);
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .contact-content {
      flex-direction: column;
    }
  
    .map-container,
    .contact-form {
      max-width: 100%;
    }
  }
  